// Import Libraries and Data
import React from 'react';
import { DataGrid } from '@mui/x-data-grid';
import multiColumnSort from 'multi-column-sort';
import { useStateWithCallbackLazy } from 'use-state-with-callback';
import watchlistData from './watchlistData/watchlist_data.json'


// Parse watchlist_data.json
let rowArray = [];
let tempId = 1;
for (const tickerSymbol in watchlistData) {
  let rowObj = {}

  rowObj['id'] = tempId;
  rowObj['ticker'] = tickerSymbol;
  
  for (const i in watchlistData[tickerSymbol]) {
    rowObj[i] = watchlistData[tickerSymbol][i];
  }

  tempId++;
  rowArray.push(rowObj);
}


function App() {
  // Initialize states
  const [sortObjState, setSortObjState] = useStateWithCallbackLazy({
    'ticker': '',
    'sigma_l': '',
    'ivc': '',
    'est_date': '',
    'fld': ''
  });

  const [sortOrderState, setSortOrderState] = useStateWithCallbackLazy({
    'ticker': 0,
    'sigma_l': 0,
    'ivc': 0,
    'est_date': 0,
    'fld': 0
  });

  const [headerNameState, setHeaderNameState] = useStateWithCallbackLazy({
    'ticker': 'Ticker',
    'sigma_l': 'Sigma L',
    'ivc': 'IVC',
    'est_date': 'Est. Date',
    'fld': 'FLD'
  });

  const [activeSortingState, setActiveSortingState] = useStateWithCallbackLazy(rowArray);

  const columns = [
    { field: 'ticker', headerName: headerNameState['ticker'], width: 75, sortable: false },
    { field: 'sigma_l', headerName: headerNameState['sigma_l'], width: 75, sortable: false },
    { field: 'ivc', headerName: headerNameState['ivc'], width: 75, sortable: false },
    { field: 'est_date', headerName: headerNameState['est_date'], width: 150, sortable: false, valueFormatter: (params) => {
        if (params.value == null) {
          return '';
        }
        let formattedDate = new Date(params.value);
        return formattedDate.toLocaleString('en-US', {year: 'numeric', month: 'long', day: 'numeric'});
      } 
    },
    { field: 'fld', headerName: headerNameState['fld'], width: 75, sortable: false }
  ];
  
  // Helper functions
  const addToSortOrder = (sortOrderObj) => {
    let arr = Object.values(sortOrderObj);
    return Math.max(...arr) + 1
  }

  const deleteFromSortOrder = (keyPair) => {
    const cloneSortOrderObj = {...sortOrderState};
    for (let k in keyPair) {
      cloneSortOrderObj[k] = keyPair[k]
    }

    let arr = [];
    for (const item in cloneSortOrderObj) {
      if (cloneSortOrderObj[item] > 0) {
        arr.push([item, cloneSortOrderObj[item]]);
      }
    }

    let sortedArr = arr.sort((a, b) => {
      return a[1] - b[1];
    });

    let idx = 1;
    sortedArr.forEach(element => {
      element[1] = idx;
      idx++;
    });

    const mergeSortObj = Object.fromEntries(sortedArr);
    Object.keys(cloneSortOrderObj).map(key => {
      if (key in mergeSortObj) {
        cloneSortOrderObj[key] = mergeSortObj[key];
      }
    });

    return cloneSortOrderObj;
  }

  const generateActiveSortingObj = (currSortOrder, currSortObj) => {
    let tempSortingArr = [];
    for (const key in currSortOrder) {
      if (currSortOrder[key] > 0) {
        tempSortingArr.push([key, currSortOrder[key]]);
      }
    }

    tempSortingArr.sort((a, b) => {
      return a[1] - b[1];
    });

    const tempSortingObj = Object.fromEntries(tempSortingArr);
    Object.keys(tempSortingObj).map(key => {
      if (key in currSortObj) {
        tempSortingObj[key] = currSortObj[key];
      }
    });

    let sortedRows = [];
    if (Object.keys(tempSortingObj).length === 0) {
      sortedRows = rowArray;
    } else {
      sortedRows = multiColumnSort(rowArray, tempSortingObj);
      //console.log(sortedRows);
    }

    return sortedRows;
  }
  
  const handleColumnHeaderClick = (e) => {
    switch (e.field) {
      case 'ticker':
        switch (sortObjState['ticker']) {
          case '':
            setSortOrderState({...sortOrderState, ticker: addToSortOrder(sortOrderState)}, (currSortOrder) => {
              setSortObjState({...sortObjState, ticker: 'ASC'}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, ticker: `Ticker ${currSortObj['ticker']} ${currSortOrder['ticker'] > 0 ? currSortOrder['ticker'] : ''}`});
              });
            });
            break;
          case 'ASC':
            setSortObjState({...sortObjState, ticker: 'DESC'}, (currSortObj) => {
              setActiveSortingState(generateActiveSortingObj(sortOrderState, currSortObj));
              setHeaderNameState({...headerNameState, ticker: `Ticker ${currSortObj['ticker']} ${sortOrderState['ticker']}`});
            });
            break;
          case 'DESC':
            setSortOrderState(deleteFromSortOrder({ticker: 0}), (currSortOrder) => {
              setSortObjState({...sortObjState, ticker: ''}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, 
                  ticker: `Ticker ${currSortObj['ticker']} ${currSortOrder['ticker'] > 0 ? currSortOrder['ticker'] : ''}`,
                  sigma_l: `Sigma L ${currSortObj['sigma_l']} ${currSortOrder['sigma_l'] > 0 ? currSortOrder['sigma_l'] : ''}`,
                  ivc: `IVC ${currSortObj['ivc']} ${currSortOrder['ivc'] > 0 ? currSortOrder['ivc'] : ''}`,
                  est_date:`Est. Date ${currSortObj['est_date']} ${currSortOrder['est_date'] > 0 ? currSortOrder['est_date'] : ''}`,
                  fld: `FLD ${currSortObj['fld']} ${currSortOrder['fld'] > 0 ? currSortOrder['fld'] : ''}`
                });
              });
            });
            break;
          default:
            break;
        }
        break;
      case 'sigma_l':
        switch (sortObjState['sigma_l']) {
          case '':
            setSortOrderState({...sortOrderState, sigma_l: addToSortOrder(sortOrderState)}, (currSortOrder) => {
              setSortObjState({...sortObjState, sigma_l: 'ASC'}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, sigma_l: `Sigma L ${currSortObj['sigma_l']} ${currSortOrder['sigma_l'] > 0 ? currSortOrder['sigma_l'] : ''}`});
              });
            });
            break;
          case 'ASC':
            setSortObjState({...sortObjState, sigma_l: 'DESC'}, (currSortObj) => {
              setActiveSortingState(generateActiveSortingObj(sortOrderState, currSortObj));
              setHeaderNameState({...headerNameState, sigma_l: `Sigma L ${currSortObj['sigma_l']} ${sortOrderState['sigma_l']}`});
            });
            break;
          case 'DESC':
            setSortOrderState(deleteFromSortOrder({sigma_l: 0}), (currSortOrder) => {
              setSortObjState({...sortObjState, sigma_l: ''}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, 
                  ticker: `Ticker ${currSortObj['ticker']} ${currSortOrder['ticker'] > 0 ? currSortOrder['ticker'] : ''}`,
                  sigma_l: `Sigma L ${currSortObj['sigma_l']} ${currSortOrder['sigma_l'] > 0 ? currSortOrder['sigma_l'] : ''}`,
                  ivc: `IVC ${currSortObj['ivc']} ${currSortOrder['ivc'] > 0 ? currSortOrder['ivc'] : ''}`,
                  est_date:`Est. Date ${currSortObj['est_date']} ${currSortOrder['est_date'] > 0 ? currSortOrder['est_date'] : ''}`,
                  fld: `FLD ${currSortObj['fld']} ${currSortOrder['fld'] > 0 ? currSortOrder['fld'] : ''}`
                });
              });
            });
            break;
          default:
            break;
        }
        break;
      case 'ivc':
        switch (sortObjState['ivc']) {
          case '':
            setSortOrderState({...sortOrderState, ivc: addToSortOrder(sortOrderState)}, (currSortOrder) => {
              setSortObjState({...sortObjState, ivc: 'ASC'}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, ivc: `IVC ${currSortObj['ivc']} ${currSortOrder['ivc'] > 0 ? currSortOrder['ivc'] : ''}`});
              });
            });
            break;
          case 'ASC':
            setSortObjState({...sortObjState, ivc: 'DESC'}, (currSortObj) => {
              setActiveSortingState(generateActiveSortingObj(sortOrderState, currSortObj));
              setHeaderNameState({...headerNameState, ivc: `IVC ${currSortObj['ivc']} ${sortOrderState['ivc']}`});
            });
            break;
          case 'DESC':
            setSortOrderState(deleteFromSortOrder({ivc: 0}), (currSortOrder) => {
              setSortObjState({...sortObjState, ivc: ''}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, 
                  ticker: `Ticker ${currSortObj['ticker']} ${currSortOrder['ticker'] > 0 ? currSortOrder['ticker'] : ''}`,
                  sigma_l: `Sigma L ${currSortObj['sigma_l']} ${currSortOrder['sigma_l'] > 0 ? currSortOrder['sigma_l'] : ''}`,
                  ivc: `IVC ${currSortObj['ivc']} ${currSortOrder['ivc'] > 0 ? currSortOrder['ivc'] : ''}`,
                  est_date:`Est. Date ${currSortObj['est_date']} ${currSortOrder['est_date'] > 0 ? currSortOrder['est_date'] : ''}`,
                  fld: `FLD ${currSortObj['fld']} ${currSortOrder['fld'] > 0 ? currSortOrder['fld'] : ''}`
                });
              });
            });
            break;
          default:
            break;
        }
        break;
      case 'est_date':
        switch (sortObjState['est_date']) {
          case '':
            setSortOrderState({...sortOrderState, est_date: addToSortOrder(sortOrderState)}, (currSortOrder) => {
              setSortObjState({...sortObjState, est_date: 'ASC'}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, est_date: `Est. Date ${currSortObj['est_date']} ${currSortOrder['est_date'] > 0 ? currSortOrder['est_date'] : ''}`});
              });
            });
            break;
          case 'ASC':
            setSortObjState({...sortObjState, est_date: 'DESC'}, (currSortObj) => {
              setActiveSortingState(generateActiveSortingObj(sortOrderState, currSortObj));
              setHeaderNameState({...headerNameState, est_date: `Est. Date ${currSortObj['est_date']} ${sortOrderState['est_date']}`});
            });
            break;
          case 'DESC':
            setSortOrderState(deleteFromSortOrder({est_date: 0}), (currSortOrder) => {
              setSortObjState({...sortObjState, est_date: ''}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, 
                  ticker: `Ticker ${currSortObj['ticker']} ${currSortOrder['ticker'] > 0 ? currSortOrder['ticker'] : ''}`,
                  sigma_l: `Sigma L ${currSortObj['sigma_l']} ${currSortOrder['sigma_l'] > 0 ? currSortOrder['sigma_l'] : ''}`,
                  ivc: `IVC ${currSortObj['ivc']} ${currSortOrder['ivc'] > 0 ? currSortOrder['ivc'] : ''}`,
                  est_date:`Est. Date ${currSortObj['est_date']} ${currSortOrder['est_date'] > 0 ? currSortOrder['est_date'] : ''}`,
                  fld: `FLD ${currSortObj['fld']} ${currSortOrder['fld'] > 0 ? currSortOrder['fld'] : ''}`
                });
              });
            });
            break;
          default:
            break;
        }
        break;
      case 'fld':
        switch (sortObjState['fld']) {
          case '':
            setSortOrderState({...sortOrderState, fld: addToSortOrder(sortOrderState)}, (currSortOrder) => {
              setSortObjState({...sortObjState, fld: 'ASC'}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, fld: `FLD ${currSortObj['fld']} ${currSortOrder['fld'] > 0 ? currSortOrder['fld'] : ''}`});
              });
            });
            break;
          case 'ASC':
            setSortObjState({...sortObjState, fld: 'DESC'}, (currSortObj) => {
              setActiveSortingState(generateActiveSortingObj(sortOrderState, currSortObj));
              setHeaderNameState({...headerNameState, fld: `FLD ${currSortObj['fld']} ${sortOrderState['fld']}`});
            });
            break;
          case 'DESC':
            setSortOrderState(deleteFromSortOrder({fld: 0}), (currSortOrder) => {
              setSortObjState({...sortObjState, fld: ''}, (currSortObj) => {
                setActiveSortingState(generateActiveSortingObj(currSortOrder, currSortObj));
                setHeaderNameState({...headerNameState, 
                  ticker: `Ticker ${currSortObj['ticker']} ${currSortOrder['ticker'] > 0 ? currSortOrder['ticker'] : ''}`,
                  sigma_l: `Sigma L ${currSortObj['sigma_l']} ${currSortOrder['sigma_l'] > 0 ? currSortOrder['sigma_l'] : ''}`,
                  ivc: `IVC ${currSortObj['ivc']} ${currSortOrder['ivc'] > 0 ? currSortOrder['ivc'] : ''}`,
                  est_date:`Est. Date ${currSortObj['est_date']} ${currSortOrder['est_date'] > 0 ? currSortOrder['est_date'] : ''}`,
                  fld: `FLD ${currSortObj['fld']} ${currSortOrder['fld'] > 0 ? currSortOrder['fld'] : ''}`
                });
              });
            });
            break;
          default:
            break;
        }
        break;
      default:
        console.log('idk');
    }
  };

  // Render DataGrid
  return (
    <div style={{ height: '100vh', width: '100%' }}>
      <DataGrid onColumnHeaderClick={(e) => handleColumnHeaderClick(e)} columns={columns} rows={activeSortingState} disableColumnMenu />
    </div>
  );
}

export default App;